import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './menu.css';
import App from './App';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Routes>
        <Route path='*' element={<App />} />
      </Routes>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);